.catalog-item{
  padding-bottom: 5em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 8.5575em) 3.1875em;
    gap: 5.37em;
    margin-top: 1.875em;
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 34.875em) 32.5em;
    gap: 1.875em;
  }
  &__col{
    display: flex;
    flex-direction: column;
  }
  &__favourite{
    display: flex;
    flex-direction: column;
    align-items: center;
    position: sticky;
    top: 1em;
    &-title{
      color: #B4B4B4;
      margin-top: .625em;
      writing-mode: tb-rl;
      transform: rotate(180deg);
      font-weight: 600;
      letter-spacing: 0.03em;
      text-transform: uppercase;
      span{
        font-size: .75em;
        line-height: 167%;
      }
    }
    .favourite-btn{
      width: 3.1875em;
      height: 3.1875em;
    }
  }
}

.gallery{
  display: grid;
  grid-template-columns: 6.75em calc(100% - 8.625em);
  gap: 1.875em;
  &__img{
    @include imgCover(75.4%);
    display: block;
  }
  &__small,
  &__big{
    width: 100%;
    height: 100%;
  }
  &__small{
    .swiper{
      height: calc(100% + 1.875em);
      &-slide{
        height: 20%;
        padding-bottom: 1.875em;
        img{
          width: 100%;
          height: 100%;
          object-fit: cover;
          display: block;
        }
      }
    }
  }
  &__big{
    position: relative;
    .gallery__img{
      min-height: 100%;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    right: 0;
    bottom: 0;
    width: 100%;
    padding: 1.875em;
    position: absolute;
    z-index: 2;
    pointer-events: none;
  }
  &__zoom{
    width: 2.5em;
    display: block;
    pointer-events: none;
  }
  .swiper-pagination{
    width: unset;
    position: static;
  }
}

.product-info{
  &__top{
    @include grid100gap(.375em);
  }
  &__title{
    font-size: 2.3125em;
    line-height: 127%;
    font-weight: 600;
  }
  &__prices{
    margin-top: 1.5em;
    display: flex;
    align-items: center;
  }
  &__price1,
  &__price2{
    display: flex;
    align-items: center;
  }
  &__price1{
    margin-right: 1.625em;
    text-decoration-line: line-through;
    color: #8B9092;
    &-title{
      font-size: 1.3125em;
    }
    &-desc{
      font-size: 1em;
      margin-left: .19em;
    }
  }
  &__price2{
    &-title{
      font-size: 1.75em;
      line-height: 72%;
    }
    &-desc{
      color: #8B9092;
      font-size: 1.25em;
      margin-left: .5em;
    }
  }
  &__nav{
    margin-top: 2.375em;
    display: grid;
    grid-template-columns: calc(56% - .625em) calc(44% - .625em);
    gap: 1.25em;
    justify-content: space-between;
    .m-btn-black{
      text-transform: uppercase;
      font-weight: 800;
      span{
        font-size: .8125em;
      }
    }
    .m-btn-black-outline{
      font-weight: 400;
      span{
        font-size: .9375em;
      }
    }
    ._full{
      grid-column: 1/-1;
    }
  }
  &__btn{
    width: 100%;
    height: 4.0625em;
  }
}

.props{
  margin-top: 1.25em;
}

.prop{
  display: flex;
  justify-content: space-between;
  padding: .94em 0;
  border-bottom: 1px dashed #D9D9D9;
  &__title,
  &__desc{
    font-size: .9375em;
    line-height: 133%;
    font-weight: 400;
  }
  &__title{
    color: #666;
  }
  &__colors{
    display: flex;
    align-items: center;
  }
  &__color{
    width: 1.5em;
    height: 1.5em;
    margin-left: .625em;
    flex-shrink: 0;
    border-radius: 50%;
    position: relative;
    z-index: 3;
    &:hover{
      .prop__color-hint{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &-hint{
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      position: absolute;
      top: 99%;
      left: 50%;
      transform: translateX(-50%);
      padding-top: .5em;
      z-index: 2;
      &-inner{
        background: rgba(0, 0, 0, 0.52);
        padding: .625em;
        @include center;
        min-width: 5.25em;
        color: #fff;
        position: relative;
        &:before{
          content: '';
          width: .5em;
          height: .25em;
          position: absolute;
          bottom: 100%;
          left: 50%;
          transform: translateX(-50%);
          background-color: inherit;
          clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
        }
      }
      span{
        font-weight: 800;
        font-size: 0.8125em;
        line-height: 116%;
        letter-spacing: 0.00813em;
      }
    }
  }
}

.product-advantages{
  padding-top: 2.81em;
  margin-top: auto;
  display: grid;
  grid-template-columns: calc(42% - 1.625em) calc(58% - 1.625em);
  gap: 1.44em 3.25em;
}

.product-advantage{
  display: flex;
  color: #6A6A6A;
  &__icon{
    width: 2.9375em;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    line-height: 147%;
    font-weight: 400;
  }
}

.product-detail{
  margin-top: 5em;
}

.guides{
  @include grid100gap(1.25em)
}

.guide-card{
  &__show{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.75em;
    background-color: #fff;
  }
  &__hidden{
    display: none;
    padding: 1.88em 1.56em .625em;
  }
  &__title{
    text-transform: uppercase;
    font-size: 1.1875em;
    line-height: 116%;
    font-weight: 600;
    letter-spacing: 0.01188em;
  }
  &__btn{
    flex-shrink: 0;
    width: 1em;
    height: 1em;
    position: relative;
    margin-left: .5em;
    flex-shrink: 0;
    &:hover{
      path{
        stroke: $darkGray;
      }
    }
    svg{
      @include centerAbsolute;
      width: 100%;
      height: auto;
    }
    ._view2{
      opacity: 0;
      pointer-events: none;
      width: .875em;
    }
  }
  &._active{
    .guide-card__btn{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
  .article-content{
    margin: 0;
    p,li{
      font-size: .9375em;
      line-height: 200%;
      @include lastM(0 0 1em);
      & * {
        line-height: inherit;
      }
    }
    ul{
      padding-left: .81em;
    }
  }
  ._grid2{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.45em));
    gap: 2.9em;
  }
}

.mark-text{
  display: inline-flex;
  align-items: center;
  border: 1px dashed #DADADA;
  background: #FFF;
  padding: 0 .625em;
  margin: 0 .625em;
}

.advantages2{
  padding: 3.12em;
  background-color: #fff;
  @include grid100gap(2.88em);
  .advantage-item{
    &__icon{
      width: 2.9375em;
      height: 2.9375em;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    padding-bottom: 2.5em;
    &__inner{
      grid-template-columns: calc(100% - 4.6875em) 3.1875em;
      gap: 1.5em;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .gallery{
    grid-template-columns: 6em calc(100% - 7em);
    gap: 1em;
    &__small{
      .swiper{
        height: calc(100% + 1em);
        &-slide{
          padding-bottom: 1em;
        }
      }
    }
  }

  .guide-card{
    ._grid2{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .product-detail{
    margin-top: 3em;
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    overflow: hidden;
    &__inner{
      margin: 0;
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__sidebar{
      order: -1;
    }
    &__favourite{
      flex-direction: row;
      &-title{
        writing-mode: unset;
        transform: unset;
        margin: 0 0 0 1em;
      }
    }
  }

  .gallery{
    grid-template-columns: 100%;
    &__big{
      overflow: unset;
    }
    &__nav,
    &__small{
      display: none;
    }
  }

  .product-info{
    &__title{
      font-size: 1.75em;
    }
    &__nav{
      grid-template-columns: 100%;
      gap: .5em;
      margin-top: 1em;
    }
  }

  .product-advantages{
    grid-template-columns: 100%;
  }

  .product-advantage{
    align-items: center;
  }

  .guide-card{
    &__show{
      padding: 1em;
    }
    &__hidden{
      padding: .625em 1em;
    }
    &__title{
      font-size: 1.125em;
    }
  }

  .advantages2{
    padding: 1em;
    gap: 1.5em;
    .advantage-item{
      &__icon{
        width: 2em;
        height: 2em;
      }
    }
  }
}
