.header{
  padding: .875em 0;
  background-color: #fff;
  position: relative;
  z-index: 10;
  &__inner{
    position: relative;
    display: grid;
    grid-template-columns: calc(50% - 2.5em) 4.375em calc(50% - 2.5em);
    gap: .3125em;
    align-items: center;
  }
  &__logo{
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__right{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__mail{
    margin-right: 4em;
    display: flex;
    align-items: center;
    svg{
      width: 1.25em;
      height: auto;
      margin-right: .4em;
      flex-shrink: 0;
    }
    span{
      font-size: 0.8125em;
      line-height: 146%;
      border-bottom: 1px solid rgba($black,.3);
      transition: inherit;
    }
    &:hover{
      path{
        stroke: $primary;
      }
      span{
        border-color: transparent;
      }
    }
  }
  .footer__social-items{
    display: none;
  }
  &._open-search{
    .search{
      transform: translateX(0%);
      z-index: 30;
      opacity: 1;
      pointer-events: unset;
      &__bg{
        display: block;
      }
    }
  }
  &:not(._open-search){
    .search{
      height: 0;
    }
  }
}

.nav{
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  &__item{
    position: relative;
    &._active{
      .nav-dropdown{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &__link,
  &__item{
    font-weight: 500;
    @include lastM(0 2.81em 0 0);
  }
  &__link,
  &__show{
    display: flex;
    align-items: center;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      color: $primary;
    }
    span{
      font-size: .875em;
      line-height: 135%;
    }
  }
  &__icon{
    width: .75em;
    position: relative;
    margin-left: .25em;
    img{
      @include centerAbsolute;
      width: 100%;
    }
  }
  &__burger{
    width: 1.125em;
    flex-shrink: 0;
    margin-right: .59em;
  }
  .nav-dropdown{
    width: 16em;
  }
}

.nav-dropdown{
  position: absolute;
  top: 100%;
  z-index: 10;
  margin-top: 2.87em;
  left: -1.25em;
  transition: all .3s;
  opacity: 0;
  pointer-events: unset;
  &__item{
    background-color: #fff;
    border-bottom: 1px solid #EEE;
    display: flex;
    align-items: center;
    padding: .5em 1.25em;
    white-space: nowrap;
    min-height: 3.0625em;
    span{
      font-size: .8125em;
      line-height: 146%;
    }
    &:hover,
    &._active{
      background: #1D1D1B;
      border-color: #1D1D1B;
      color: #fff;
    }
  }
}

.header-action{
  display: flex;
  align-items: center;
  &__btn{
    max-width: 1.375em;
    height: 1.375em;
    position: relative;
    @include lastM(0 2.5em 0 0);
    svg{
      width: auto;
      height: 100%;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
    &-num{
      position: absolute;
      top: -.81em;
      right: -.81em;
      width: 1.125em;
      height: 1.125em;
      @include center;
      color: #9F9F9F;
      font-weight: 300;
      span{
        font-size: .75em;
      }
    }
  }
}

._nav-parent{
  position: relative;
  &._active{
    .nav-dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.header-lang{
  margin-right: 3em;
  .nav-dropdown{
    width: 6em;
  }
}

.header-currency{
  margin-right: 3em;
  .nav-dropdown{
    width: 5.25em;
  }
}

.menu{
  display: flex;
  align-items: center;
  &__top{
    display: flex;
    align-items: center;
  }
}

.menu__close,
.burger{
  display: none;
}

.search{
  transition: all .4s;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  z-index: -1;
  bottom: 0;
  left: 0;
  width: 100%;
  height: calc(100vh - 4.94em);
  background: #FFF;
  box-shadow: 0px -10px 63px 0px rgba(0, 0, 0, 0.25);
  //transform: translateX(-150%);
  &__bg{
    z-index: 28;
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $fullHeight;
    background: rgba(#1B1B1B, 0.60);
  }
  &__top{
    position: relative;
  }
  &__field{
    height: 7.44em;
    input{
      width: 100%;
      height: 100%;
      border: none;
      border-bottom: 1px solid #D9D9D9;
      text-align: center;
      padding: 0 2em;
      font-size: 2.6875em;
      font-weight: 700;
      &::placeholder{
        color: rgba(#000,.1);
      }
    }
  }
  &__close{
    position: absolute;
    width: 1.875em;
    right: 1.5em;
    top: 50%;
    transform: translateY(-50%);
    img{
      width: 100%;
      transition: all .3s;
    }
    &:hover{
      img{
        transform: scale(1.1);
      }
    }
  }
  &__content{
    padding: 4.75em 0;
    height: calc(100vh - 4.94em - 7.44em);
    overflow-y: auto;
  }
  &__items{
    margin-top: 4em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 5.625em / 4));
    gap: 1.875em;
  }
  &__empty{
    text-align: center;
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: .56em 0;
    &__mail{
      order: 2;
      margin: 2.38em auto 0;
    }
    &__inner{
      grid-template-columns: calc(50% - 3.25em) 2.75em calc(50% - 3.25em);
      gap: 1.875em;
    }
    &._open{
      .menu{
        transform: translateX(0);
      }
    }
    .footer__social-items{
      display: flex;
      order: 3;
      margin: 1.5em 0 0;
    }
    .footer__social-item{
      border-color: rgba(80, 80, 80, 0.10);
      &:hover{
        border-color: $black;
      }
    }
  }

  .header-action{
    &__btn{
      @include lastM(0 1.625em 0 0);
      max-width: 1.125em;
      max-height: 1.125em;
      &-num{
        top: -.75em;
        right: -.69em;
        span{
          font-size: .6875em;
        }
      }
      &._search-btn{
        position: absolute;
        left: 2.75em;
        top: 50%;
        transform: translateY(-50%);
        width: .9375em;
      }
    }
  }

  .burger{
    width: 1.125em;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }

  .menu{
    flex-direction: column;
    position: fixed;
    transition: all .5s;
    transform: translateX(-150%);
    overflow-y: auto;
    height: $fullHeight;
    width: 100%;
    left: 0;
    top: 0;
    background-color: #fff;
    z-index: 30;
    &__top{
      width: 100%;
      padding: 1.2em .94em 2.94em;
      justify-content: flex-end;
      border-bottom: 1px solid #F2F2F2;
    }
    &__close{
      display: block;
      margin-right: auto;
      width: .8125em;
      img{
        width: 100%;
        display: block;
      }
    }
  }

  .header-lang{
    margin-right: 2.4em;
  }

  .header-currency{
    margin-right: 0;
    .nav-dropdown{
      width: 4.5em;
    }
  }

  .nav-dropdown{
    box-shadow: 0 0 .9375em rgba(0,0,0,.15);
    margin-top: 1em;
    background-color: #fff;
  }

  .nav{
    width: 100%;
    position: static;
    flex-direction: column;
    transform: unset;
    margin-top: .5em;
    &__item{
      &._active{
        .nav__show{
          background: #F5F5F5;
          color: $black;
        }
      }
    }
    &__link,
    &__item{
      width: 100%;
      margin: 0 !important;
    }
    .nav__link,
    .nav__show{
      border-bottom: 1px solid #F2F2F2;
      height: 3.6875em;
      padding: 0 1.875em;
      font-weight: 600;
      text-transform: uppercase;
      &:hover{
        color: $black;
      }
    }
    &__burger{
      display: none;
    }
    .nav__icon{
      margin-left: auto;
    }
    .nav-dropdown{
      position: static;
      opacity: 1;
      pointer-events: unset;
      width: 100%;
      box-shadow: unset;
      margin: 0;
      transition: unset;
      display: none;
    }
    .nav-dropdown__item{
      padding-left: 2.5em;
    }
  }

  .search{
    height: calc(100 * var(--vh) - 3em);
    &__field{
      height: 4em;
      input{
        font-size: 1.25em;
      }
    }
    &__close{
      width: 1.25em;
    }
    &__content{
      padding: 2em 0;
      height: calc(100 * var(--vh) - 7em);
    }
    &__items{
      margin-top: 1em;
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .search{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
