.instagram-card{
  &__img{
    display: block;
    @include imgCover(100%);
  }
}

.product-card{
  position: relative;
  .favourite-btn{
    position: absolute;
    top: 1.25em;
    right: 1.25em;
    z-index: 3;
  }
  &__slider{
    overflow: hidden;
    position: relative;
  }
  &__img{
    display: block;
    @include imgCover(102%);
  }
  .swiper{
    &-pagination{
      position: absolute;
      padding: .94em .84em;
      height: 100%;
      width: 100%;
      left: 0;
      bottom: 0;
      z-index: 2;
      &-bullet{
        flex-grow: 1;
        margin: 0 .1em !important;
        height: 100%;
        border-radius: 0;
        background-color: transparent;
        position: relative;
        &:before{
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 2px;
          background: rgba(255, 255, 255, 0.74);
          transition: all .3s;
        }
        &-active:before{
          background: #1A1C1D;
        }
      }
    }
  }
  &__content{
    margin-top: 1.44em;
    @include grid100gap(.5em);
  }
  &__info{
    display: flex;
    align-items: center;
  }
  &__price{
    color: #454A4C;
    font-size: 1.0625em;
    line-height: 118%;
    margin-right: .3em;
  }
  &__measure{
    font-size: .9375em;
    line-height: 133%;
    color: #8B9092;
  }
}

.blog-card{
  &__img{
    display: block;
    @include imgCover(57.1%);
    overflow: hidden;
    &:hover{
      img{
        transform: scale(1.2);
      }
    }
    img{
      transition: all .9s;
    }
  }
  &__content{
    margin-top: .75em;
    @include grid100gap(.75em);
  }
  &__desc{
    color: #333;
    font-weight: 400;
    line-height: 193%;
  }
  &__nav{
    display: flex;
  }
}

.blog-card2{
  .blog-card__img{
    padding-bottom: 82.5%;
  }
}

.collection-card{
  display: flex;
  align-items: center;
  justify-content: space-between;
  @media screen and (min-width: 993px){
    &:nth-child(even){
      flex-direction: row-reverse;
      .collection-card__img{
        margin: 0 6.25em 0 0;
      }
    }
  }
  &__img{
    width: 51.9375em;
    flex-shrink: 0;
    margin-left: 6.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    width: calc(100% - 59em);
  }
  &__info{
    max-width: 32.1875em;
    @include grid100gap(1.625em);
  }
  &__title{
    font-size: 2.1875em;
    line-height: 120%;
    font-weight: 600;
    text-transform: uppercase;
  }
  &__desc{
    color: #424242;
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  .line-link{
    font-weight: 800;
    margin-left: 3.125em;
    height: 2.4375em;
    align-items: center;
    text-transform: uppercase;
    span{
      font-size: .8125em;
    }
    img{
      margin-left: 1.94em;
      width: 1.3125em;
      flex-shrink: 0;
    }
  }
}

.category-card{
  display: block;
  position: relative;
  &__img{
    overflow: hidden;
    @include imgCover(156.6%);
    &:hover{
      img{
        transform: scale(1.3);
      }
    }
    img{
      transition: all 1.2s;
    }
  }
  &__content{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 1em 1em 3.9em;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    color: #fff;
    z-index: 3;
  }
  &__count{
    @include center;
    padding: 0 1.44em;
    height: 2.3125em;
    border: 1px solid #fff;
    margin-bottom: 1.2em;
    span{
      font-size: .8125em;
      line-height: 130%;
      letter-spacing: 0.02438em;
      text-transform: uppercase;
    }
  }
  &__title{
    font-size: 2.25em;
    font-weight: 400;
    line-height: 109%;
    letter-spacing: 0.0675em;
  }
}

.work-card{
  &__img{
    @include imgCover(100%);
  }
}

.portfolio-card{
  &__img{
    @include imgCover(57%);
  }
  &__content{
    margin-top: 1.2em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-size: 1.25em;
    font-weight: 600;
    text-transform: uppercase;
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  &__btn{
    padding: 0 2em;
    height: 3.125em;
    font-weight: 800;
    margin-left: 1.25em;
    white-space: nowrap;
    span{
      font-size: 0.8125em;
      letter-spacing: 0.00813em;
    }
    img{
      width: 1.3125em;
      margin-left: .81em;
    }
  }
}


@media screen and (max-width: 992px){
  .collection-card{
    flex-direction: column;
    justify-content: unset;
    &__img{
      order: -1;
      margin: 0 0 2em;
      width: 100%;
    }
    &__content{
      width: 100%;
    }
    &__info{
      gap: 1em;
    }
    &__title{
      font-size: 1.75em;
    }
    &__nav{
      flex-direction: column;
      align-items: flex-start;
      .m-btn{
        width: 100%;
      }
    }
    .line-link{
      margin: 1em 0 0;
    }
  }

  .product-card{
    .swiper-pagination{
      height: 2px;
    }
  }
}


@media screen and (max-width: 576px){
  .blog-card{
    &__img{
      padding-bottom: 70%;
    }
    &__content{
      gap: .5em;
    }
    .text15{
      font-size: .875em;
    }
  }

  .category-card{
    &__img{
      padding-bottom: 130%;
    }
    &__content{
      padding-bottom: 2em;
    }
  }

  .portfolio-card{
    &__content{
      display: block;
    }
    &__nav{
      margin-top: 1em;
      @include grid100gap(.5em);
    }
    &__btn{
      width: 100%;
      margin: 0;
    }
  }
}
