.main{
  position: relative;
  &__mouse{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 1.81em;
    width: 1.125em;
    z-index: 2;
    img{
      width: 100%;
      display: block;
      animation: 3s infinite alternate moveMouse;
    }
  }
}

.main-slider{
  color: #fff;
  position: relative;
  overflow: hidden;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-button{
      position: static;
      margin: 0 1.75em 0 0;
    }
    &-pagination{
      position: static;
      width: unset;
      margin-left: auto;
    }
  }
  .container{
    height: 100%;
  }
  &__nav{
    position: absolute;
    display: flex;
    align-items: center;
    left: 0;
    bottom: 5em;
    padding: 0 9em;
    width: 100%;
  }
}

.main-slide{
  position: relative;
  &__inner{
    height: 100%;
    display: block;
  }
  &__img{
    width: 100%;
    display: block;
    height: 100%;
    object-fit: cover;
  }
  &__content{
    position: absolute;
    left: 0;
    bottom: 11em;
    width: 100%;
    max-height: calc(100% - 11em);
  }
  &__top{
    margin-bottom: 1em;
    @include grid100gap(1.37em);
  }
  &__nav{
    display: flex;
    margin-top: 2em;
  }
}

@keyframes moveMouse {
  0% {
    transform: translateY(0);
  }
  25% {
    transform: translateY(.625em);
  }
  50% {
    transform: translateY(0);
  }
  75% {
    transform: translateY(-.625em);
  }
  100% {
    transform: translateY(0);
  }
}

.instagram{
  padding: 3em 0 4em;
  &__top{
    display: flex;
    justify-content: center;
  }
  &__link{
    display: flex;
    align-items: center;
    img{
      width: 3.0625em;
      flex-shrink: 0;
      margin-right: 1.06em;
    }
    span{
      font-size: 1.8125em;
      line-height: 125%;
    }
  }
}

.article-slider,
.blog-slider,
.instagram-slider{
  margin-top: 2.87em;
  overflow: hidden;
  .swiper{
    &-container{
      width: calc(100% + 1.88em);
    }
    &-slide{
      width: calc(100% / 6);
      padding-right: 1.88em;
    }
  }
}

.collection{
  padding: 4.5em 0 2.5em;
}

.collection-slider{
  margin-top: 3em;
  overflow: hidden;
  &__container{
    width: calc(100% + 1.88em);
  }
  &__slide{
    padding-right: 1.88em;
    width: 25%;
  }
}

.about{
  padding: 3.4em 0 3em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 44.5125em) 40.8125em;
    gap: 3.7em;
  }
  .video{
    width: calc(100% + 9em);
    margin-left: -9em;
  }
  &__bottom{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: -3.34em;
    z-index: 2;
    position: relative;
  }
  &__items{
    display: flex;
    background: #FFF;
    border: 1px solid #F1F1F1;
  }
  &__nav{
    display: flex;
    align-items: center;
    flex-shrink: 0;
    margin-left: 3.85em;
  }
}

.about-text{
  padding-top: 4em;
  .text{
    margin-top: 1.75em;
  }
}

.about-item{
  padding: 1em 3.125em;
  min-height: 6.6875em;
  display: flex;
  align-items: center;
  font-weight: 600;
  text-transform: uppercase;
  &:not(:last-child){
    border-right: 1px solid #F1F1F1;
  }
  &__icon{
    flex-shrink: 0;
    margin-right: 2em;
    img{
      max-width: 2em;
      max-height: 1.5625em;
    }
  }
}

.advantages{
  background-color: #fff;
  padding: 3.5em 0;
  border-top: 1px solid #EDEDED;
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - (5.5em * 3 / 4)));
    gap: 5.5em;
  }
}

.advantage-item{
  display: flex;
  &__icon{
    width: 2.8125em;
    height: 2.8125em;
    flex-shrink: 0;
    margin-right: 2em;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.25em);
  }
  &__title{
    text-transform: uppercase;
  }
  &__decs{
    color: #6A6A6A;
  }
}

.blog{
  padding: 6.25em 0 3.4em;
}

.article-slider,
.blog-slider{
  overflow: hidden;
  .swiper{
    &-slide{
      width: calc(100% / 3);
    }
  }
}

.about3{
  padding: 8em 0 7.625em;
  position: relative;
  color: #FFF;
  background-size: cover;
  background-position: center;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(29, 29, 27, 0.72) 0%, rgba(29, 29, 27, 0.72) 100%);
    pointer-events: none;
  }
  &__content{
    position: relative;
    z-index: 2;
    max-width: 38.625em;
  }
  &__subtitle{
    margin-bottom: 6.44em;
    color: rgba(#fff,.7);
  }
  &__nav{
    margin-top: 7.44em;
    display: flex;
    align-items: center;
  }
  .line-link{
    color: #fff;
    height: 2.4375em;
    align-items: center;
    text-transform: uppercase;
    &:before{
      background-color: #fff;
    }
    span{
      font-size: .8125em;
    }
    img{
      margin-left: .84em;
      width: .75em;
      flex-shrink: 0;
    }
  }
  .video__play{
    margin-right: 3.75em;
    position: relative;
    top: unset;
    left: unset;
    transform: unset;
    &:not(:hover) img{
      opacity: .5;
    }
    &-title{
      position: absolute;
      bottom: 100%;
      left: -1.44em;
      margin-bottom: 2em;
      white-space: nowrap;
      color: rgba(#fff,.7);
    }
  }
}

.collection2{
  padding: 10.75em 0 7.75em;
  background-color: rgba(#D9D9D9,.2);
  &__items{
    margin: 4em auto 0;
    @include grid100gap(5em);
    width: 94.9375em;
    max-width: 100%;
  }
}

.categories{
  padding: 7.38em 0;
  &__grid{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - (1.88em * 3 / 4)));
    gap: 1.88em;
  }
}

.works{
  display: grid;
  grid-template-columns: 41.625em calc(100% - 41.625em);
  align-items: center;
  margin-top: 7.37em;
}

.works-info{
  padding: 4.2em 12.38em 4.75em 3.94em;
  background: #FFF;
  @include grid100gap(2em);
  .text17{
    line-height: 176%;
    margin-top: -.75em;
  }
  &__nav{
    display: flex;
  }
}

.works-slider{
  width: calc(100% + 7.255em);
  margin-left: -7.255em;
  overflow: hidden;
  padding: 3.69em 0;
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 2.12em);
    }
    &-slide{
      padding-right: 2.12em;
      width: calc(100% / 3);
    }
    &-nav{
      position: absolute;
      right: 0;
      bottom: 0;
      margin: 0;
    }
    &-button{
      &-next{
        margin-right: 0;
      }
    }
  }
}

.rooms{
  position: relative;
  &__bottom{
    z-index: 3;
    position: absolute;
    top: 100%;
    width: 100%;
    transform: translateY(-50%);
  }
  &__inner{
    display: flex;
    align-items: stretch;
  }
  &__link{
    flex-shrink: 0;
    padding: 0 2.62em;
    height: auto;
  }
}

.rooms-media{
  overflow: hidden;
  .swiper{
    &-slide{
      padding: 27.5em 0 8.38em;
      background-size: cover;
      background-position: center;
      position: relative;
      color: #FFF;
    }
  }
  &__content{
    @include grid100gap(.38em);
    position: relative;
    z-index: 2;
    padding: 0 6.6em;
  }
  .subtitle{
    color: #FFF;
  }
}

.room-nav{
  width: 5em;
  flex-shrink: 0;
  background-color: #fff;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    height: 1px;
    width: 100%;
    background-color: #F5F5F5;
    transform: translateY(-50%);
  }
  .swiper{
    &-button{
      position: static;
      height: 50%;
      width: 100%;
      @include center;
      margin: 0;
      img{
        width: .4375em;
      }
    }
  }
}

.room-items{
  background-color: #fff;
  .swiper{
    &-wrapper{
      transform: unset !important;
    }
    &-slide{
      position: relative;
      &:before{
        content: '';
        position: absolute;
        left: 1.63em;
        bottom: 1.75em;
        width: 2.9375em;
        height: 2.9375em;
        background-color: #FFE498;
        border-radius: 50%;
        transition: all .3s;
        opacity: 0;
      }
      &-thumb-active:before{
        opacity: 1;
      }
    }
  }
}

.room-item{
  display: flex;
  align-items: center;
  width: unset !important;
  padding: 1em 2.81em;
  height: 7.875em;
  border-left: 1px solid #F5F5F5;
  &:hover{
    color: $black;
  }
  &__icon{
    flex-shrink: 0;
    margin-right: .94em;
    position: relative;
    z-index: 2;
    img{
      height: 2.5em;
      width: auto;
    }
  }
  &__title{
    font-size: 1.0625em;
    line-height: 130%;
  }
}


@media screen and (max-width: 992px){
  .main-slider{
    &__nav{
      display: none;
    }
  }

  .main-slide{
    padding: 4em 0;
    &__inner{
      height: unset;
    }
    &__img{
      position: absolute;
      top: 0;
      left: 0;
    }
    &__content{
      position: relative;
      z-index: 2;
      max-height: unset;
      bottom: unset;
    }
    &__top{
      gap: .5em;
      margin-bottom: .5em;
    }
    &__nav{
      margin-top: 1em;
    }
  }

  .instagram{
    padding: 2em 0;
    &__link{
      img{
        width: 1.75em;
        margin-right: .625em;
      }
      span{
        font-size: 1.25em;
      }
    }
  }

  .blog-slider,
  .instagram-slider{
    margin-top: 1.5em;
    .swiper{
      &-container{
        width: calc(100% + 1em);
      }
      &-slide{
        width: calc(100% / 3);
        padding-right: 1em;
      }
    }
  }

  .collection{
    padding: 2em 0;
  }

  .collection-slider{
    margin-top: 1.5em;
    &__container{
      width: calc(100% + 1em);
    }
    &__slide{
      padding-right: 1em;
      width: calc(100% / 3);
    }
  }

  .about{
    padding: 2em 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
    }
    .video{
      width: calc(100% + 2em);
      margin-left: -1em;
    }
    &__bottom{
      margin-top: 1em;
      display: block;
    }
    &__items{
      display: grid;
      grid-template-columns: repeat(2,50%);
    }
    &__nav{
      margin: 1em 0 0;
    }
  }

  .about-text{
    padding: 0;
    .text{
      margin-top: 1.5em;
    }
  }

  .about-item{
    border-top: 1px solid #F1F1F1;
    &:first-child{
      border-top: none;
    }
    &:nth-child(even){
      border-right: none;
    }
    @media screen and (min-width: 577px){
      &:nth-child(2){
        border-top: none;
      }
    }
  }

  .advantages{
    padding: 1.5em 0;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
  }

  .advantage-item{
    &__icon{
      margin-right: 1em;
      width: 2em;
      height: 2em;
      display: flex;
      justify-content: center;
    }
  }

  .blog{
    padding: 2em 0;
  }

  .article-slider,
  .blog-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }

  .about3{
    padding: 3em 0;
    &__subtitle{
      margin-bottom: 2em;
    }
  }

  .collection2{
    padding: 3em 0;
    &__items{
      margin-top: 2em;
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 2em;
    }
  }

  .categories{
    padding: 2em 0;
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
  }

  .works{
    margin-top: 2em;
    grid-template-columns: 100%;
  }

  .works-slider{
    width: 100%;
    margin: 1em 0 0;
  }

  .works-info{
    padding: 1em 1.25em;
    gap: 1em;
  }

  .works-slider{
    padding: 0;
    .swiper{
      &-nav{
        width: 100%;
        justify-content: center;
      }
    }
  }

  .rooms{
    &__bottom{
      position: static;
      transform: unset;
      margin-top: 1em;
    }
    &__inner{
      display: block;
    }
    &__link{
      height: 3em;
      margin-top: 1em;
    }
  }

  .rooms-media{
    .swiper{
      &-slide{
        padding: 7em 0 3em;
      }
    }
    &__content{
      padding: 0;
    }
  }

  .room-nav{
    display: none;
  }

  .room-items{
    border-top: 1px solid #f5f5f5;
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(4,25%);
      }
      &-slide{
        &:before{
          left: 1em;
          bottom: 1em;
          width: 1.75em;
          height: 1.75em;
        }
      }
    }
  }

  .room-item{
    padding: .5em 1em;
    height: 4em;
    border-bottom: 1px solid #f5f5f5;
    justify-content: center;
    &__icon{
      margin-right: .5em;
      img{
        height: 1.5em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__mouse{
      bottom: .75em;
    }
  }

  .main-slide{
    padding: 10em 0 3em;
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }

  .instagram{
    overflow: hidden;
    &__top{
      justify-content: unset;
    }
    &__link{
      img{
        width: 1.25em;
      }
      span{
        font-size: 1em;
      }
    }
  }

  .works-slider,
  .instagram-slider{
    overflow: unset;
    .swiper{
      &-nav{
        display: none;
      }
      &-slide{
        width: 10em;
        padding-right: .5em;
      }
    }
  }

  .section{
    &__inner{
      position: relative;
      padding-bottom: 3.75em;
    }
    &-top{
      &__btn{
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
      }
    }
  }

  .collection{
    overflow: hidden;
  }

  .collection-slider{
    overflow: unset;
    &__slide{
      width: 16.5em;
      padding-right: 1em;
    }
    .swiper-nav{
      display: none;
    }
  }

  .about{
    &__items{
      grid-template-columns: 100%;
    }
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }

  .about-item{
    padding: 1em 1.5em;
    min-height: 4.5em;
    border-right: none !important;
    &__icon{
      width: 2em;
      flex-shrink: 0;
      margin-right: 1em;
    }
  }

  .advantages{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .article-slider,
  .blog-slider{
    overflow: unset;
    .swiper{
      &-slide{
        width: 18em;
      }
      &-nav{
        display: none;
      }
    }
  }

  .about3{
    &__nav{
      margin-top: 2em;
    }
    .video__play{
      margin-right: 1.5em;
    }
  }

  .collection2{
    overflow: hidden;
    &__items{
      grid-template-columns: 100%;
    }
  }

  .categories{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .works-info{
    .text17{
      font-size: .875em;
      margin-top: 0;
    }
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }

  .room-items {
    .swiper {
      &-wrapper {
        grid-template-columns: repeat(2, 50%);
      }
    }
  }

  .room-item{
    justify-content: unset;
    &__title{
      font-size: .875em;
    }
  }
  .blog{
    overflow: hidden;
  }
}
