.footer{
  padding: 3.25em 0 0;
  background: #1B1B1B;
  color: #fff;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 4.375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__social{
    margin-top: 1.12em;
    @include grid100gap(.94em);
    &-title{
      font-size: 0.8125em;
      font-weight: 400;
      line-height: 154%;
      letter-spacing: 0.0975em;
    }
    &-items{
      display: flex;
    }
    &-item{
      @include center;
      width: 2.9375em;
      height: 2.9375em;
      border: 1px solid rgba(255, 255, 255, 0.10);
      margin-right: .69em;
      img{
        max-width: 1.0625em;
        max-height: 1.0625em;
      }
      &:hover{
        border-color: #fff;
      }
    }
  }
  &__title{
    font-size: 1.0625em;
    font-weight: 700;
    line-height: 118%;
    letter-spacing: 0.06375em;
    text-transform: uppercase;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: .81em;
  }
  &__link{
    font-size: 0.9375em;
    font-weight: 400;
    line-height: 133%;
    letter-spacing: 0.05625em;
    color: #A8A8A8;
    @include lastM(0 0 .85em);
    &:hover{
      color: #fff;
    }
  }
  &__c{
    font-size: 0.9375em;
    line-height: 133%;
    color: #838383;
  }
  &__bottom{
    @include center;
    height: 5.3125em;
    border-top: 1px solid rgba(#fff,.05);
    margin-top: 3.6em;
  }
}

.footer-contacts{
  margin-top: .81em;
  @include grid100gap(.81em);
  &__phone{
    font-size: 1.125em;
    font-weight: 500;
    line-height: 11%;
    letter-spacing: 0.03375em;
  }
  &__time,
  &__email{
    font-size: 0.9375em;
    font-weight: 400;
    line-height: 173%;
    letter-spacing: 0.00938em;
    a{
      text-decoration: underline;
      color: #A8A8A8;
    }
  }
  &__time{
    display: flex;
    flex-direction: column;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2em 0 0;
    &__inner{
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3 - 1em));
      gap: 3em 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__inner{
      grid-template-columns: 100%;
      gap: 2em;
    }
  }
}
