.catalog{
  &__grid{
    display: grid;
    grid-template-columns: 23.9375em calc(100% - 23.9375em - 1.87em);
    gap: 1.87em;
    margin-top: 1.5em;
  }
  .page-top{
    max-width: 100%;
  }
  &__items{
    margin-top: 2.8em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - (1.88em * 2 / 3)));
    gap: 1.88em;
  }
  .about3{
    grid-column: 1/-1;
    padding: 6.31em 9em 4.31em;
    .text53{
      font-size: 2.4375em;
      line-height: 133%;
    }
    &__subtitle{
      margin-bottom: 1.2em;
    }
  }
}

.filter{
  padding: .38em 2.5em 1.2em;
  background-color: #fff;
  @include grid100gap(.5em);
  &__bg{
    display: none;
  }
}

.orders-group,
.filter-group{
  &__btn{
    flex-shrink: 0;
    width: 1em;
    height: 1em;
    position: relative;
    &:hover{
      path{
        stroke: $darkGray;
      }
    }
    svg{
      @include centerAbsolute;
      width: 100%;
      height: auto;
    }
    ._view2{
      opacity: 0;
      pointer-events: none;
      width: .875em;
    }
  }
  &._active{
    .orders-group__btn,
    .filter-group__btn{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
}

.filter-group{
  padding: 1.25em 0;
  border-bottom: 1px solid #E9E9E9;
  &__show,
  &__top{
    margin-bottom: .69em;
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &__title{
    text-transform: uppercase;
    font-size: 1.25em;
    line-height: 167%;
    font-weight: 700;
    letter-spacing: 0.01125em;
  }
  &__hidden{
    display: none;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    display: flex;
    align-items: center;
    font-weight: 400;
    @include lastM(0 0 .5em);
    span{
      font-size: 0.9375em;
      line-height: 200%;
      letter-spacing: 0.00938em;
    }
    img{
      width: .3125em;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
  &__items{
    padding: 0 .81em;
    @include grid100gap(.5em);
  }
  &__more{
    margin-top: .69em;
    height: 2.75em;
    width: 100%;
    font-weight: 400;
    span{
      font-size: .9375em;
      line-height: 150%;
    }
  }
  &:not(._all-show){
    .m-check:nth-child(8)~.m-check{
      display: none;
    }
  }
}

.filter-colors{
  display: grid;
  grid-template-columns: repeat(9,calc(100% / 9 - (.625em * 8 / 9)));
  gap: .625em;
}

.filter-color{
  display: block;
  label{
    display: block;
    padding-bottom: 100%;
    border-radius: 50%;
    position: relative;
    &:before{
      content: '';
      @include centerAbsolute;
      width: 125%;
      height: 125%;
      border: 1px solid #1D1D1B;
      border-radius: 50%;
      opacity: 0;
    }
  }
  input{
    &:checked+label {
      &::before {
        opacity: 1;
      }
    }
  }
  &._white{
    input{
      &:checked+label {
        &::before {
          background-image: url(../img/check.svg);
        }
      }
    }
    label{
      &:after{
        content: '';
        @include centerAbsolute;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        border: 1px solid #E2E2E2;
      }
    }
  }
}

.filter-nav{
  display: grid;
  grid-template-columns: 44.5% 50%;
  justify-content: space-between;
  padding: 1.0625em 0;
  &__btn{
    width: 100%;
    height: 2.75em;
    font-weight: 400;
    text-transform: uppercase;
    span{
      font-size: .9375em;
    }
  }
}

.selected-filters{
  display: flex;
  flex-wrap: wrap;
  margin-top: 1.25em;
}

.selected-filter{
  border-radius: 56.25em;
  border: 1px solid rgba(0, 0, 0, 0.10);
  display: flex;
  align-items: center;
  margin: .625em .625em 0 0;
  height: 2.5em;
  padding: 0 1.125em;
  &._clear{
    background: #E8EAEB;
    border: none;
    &:hover{
      background: #000;
      color: #fff;
    }
  }
  span{
    font-size: .875em;
    line-height: 115%;
  }
  &__remove{
    margin-left: .56em;
    flex-shrink: 0;
    &:hover{
      g{
        opacity: 1;
      }
      path{
        stroke: $black2;
      }
    }
  }
  svg{
    width: .5625em;
    height: auto;
  }
}

.sort-filter{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-top: 2em;
  &__items{
    display: flex;
    align-items: center;
  }
  &__item{
    margin-left: 1.69em;
    label{
      color: #808080;
      font-size: 1em;
      line-height: 120%;
      display: flex;
      align-items: center;
      &:before{
        content: '';
        width: 0;
        height: .25em;
        flex-shrink: 0;
        background-color: #333;
        margin-right: 0;
        border-radius: 50%;
        transition: all .3s;
      }
    }
    input{
      &:checked{
        &~label{
          color: $black2;
          &:before{
            width: .25em;
            margin-right: .44em;
          }
        }
      }
    }
  }
}

.mob-filter{
  &__btn{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .catalog{
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
      margin: 0;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
    .about3{
      padding: 3em 1em;
      .text53{
        font-size: 2.25em;
      }
    }
  }

  .mob-filter{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.5em;
    &__btn{
      display: flex;
      padding: 0;
      height: 3.5em;
      width: 3.5em;
      flex-shrink: 0;
      svg{
        width: 1.5em;
        height: auto;
      }
    }
  }

  .sort-filter{
    margin: 0;
  }

  .filter{
    position: fixed;
    top: 0;
    left: 0;
    height: $fullHeight;
    overflow-y: auto;
    width: 18em;
    max-width: 85%;
    padding-left: 1em;
    padding-right: 1em;
    z-index: 100;
    transform: translateX(-150%);
    transition: all .5s;
    &._active{
      transform: translateX(0);
      &~.filter__bg{
        display: block;
      }
    }
    &__bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $fullHeight;
      background: rgba($black2,.8);
      z-index: 99;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    .about3{
      .text53{
        font-size: 1.75em;
      }
    }
    &__items{
      grid-template-columns: 100%;
    }
  }

  .sort-filter{
    display: block;
    &__title{
      margin-bottom: .5em;
    }
    &__item{
      margin-left: 1em;
      &:first-child{
        margin-left: 0;
      }
    }
  }
}
