@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-black{
    background-color: $black2;
    color: #fff;
    height: 4.0625em;
    padding: 0 2.31em;
    font-weight: 800;
    text-transform: uppercase;
    span{
        font-size: 0.8125em;
        line-height: 130%;
        letter-spacing: 0.04063em;
    }
    img{
        width: 1.25em;
        flex-shrink: 0;
        margin-left: 1.94em;
    }
    span,img{
        transition: inherit;
    }
    &:hover{
        background: #323232;
        color: #fff;
        span,img{
            opacity: .8;
        }
    }
}


.m-btn-gray-outline{
    padding: 0 1.44em;
    color: #3C3F40;
    border: 1px solid rgba(0, 0, 0, 0.10);
    height: 2.625em;
    span{
        font-size: .9375em;
        line-height: 120%;
    }
    img{
        width: .3125em;
        flex-shrink: 0;
        margin-left: .5em;
    }
    &:hover{
        border-color: rgba(0, 0, 0, 0.60);
        color: $black2;
    }
}


.m-btn-black-outline{
    color: #1D1D1B;
    border: 1px solid #1D1D1B;
    &:hover{
        background-color: #1D1D1B;
        color: #fff;
    }
}

.m-btn-gray{
    background-color: #F6F6F6;
    color: $black;
    &:hover{
        background-color: #1D1D1B;
        color: #fff;
    }
}

.line-link{
    display: flex;
    padding: .75em 0;
    font-weight: 600;
    color: #000;
    position: relative;
    &:hover{
        color: #000;
        &:before{
            width: 120%;
        }
    }
    &:before{
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 0;
        width: 100%;
        height: 2px;
        background: #000;
        transition: all .3s;
    }
    span{
        font-size: 1.1875em;
    }
}

