@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.swiper-button{
  width: 2.625em;
  height: unset;
  flex-shrink: 0;
  img{
    width: 100%;
    display: block;
  }
  &:hover{
    opacity: .8;
  }
  &.swiper-button-disabled{
    opacity: .2;
  }
}

.swiper-nav{
  margin-top: 3em;
  @include center;
  .swiper-button{
    position: static;
    margin: 0 .875em;
  }
}

.swiper-pagination{
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  &-bullet{
    width: 0.4375em;
    height: 0.4375em;
    background-color: #fff;
    opacity: 1;
    margin: 0 .9em !important;
    &-active{
      background-color: $primary;
      width: 0.6875em;
      height: 0.6875em;
    }
  }
}

.section-top{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.favourite-btn{
  position: relative;
  width: 2.5em;
  height: 2.5em;
  background-color: #fff;
  border-radius: 50%;
  &:hover{
    background-color: darken(#fff,20%);
  }
  img{
    @include centerAbsolute;
    width: .75em;
    transition: all .3s;
  }
  ._view2{
    opacity: 0;
  }
  &._active{
    ._view1{
      opacity: 0;
    }
    ._view2{
      opacity: 1;
    }
  }
}

.cart-alert{
  position: fixed;
  right: 3.125em;
  bottom: 3.125em;
  z-index: 30;
  width: 27.4375em;
  background-color: #fff;
  padding: 1.56em 1.56em 1.875em;
  box-shadow: 0 0 0.9375em rgba(0, 0, 0, 0.15);
  display: flex;
  color: #000;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &__img{
    width: 6.5em;
    height: 6.75em;
    flex-shrink: 0;
    margin-right: 1.37em;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
  &__info{
    @include grid100gap(.3125em);
  }
  &__nav{
    display: flex;
    margin-top: .69em;
  }
  &__close{
    position: absolute;
    top: .94em;
    right: .94em;
    background-color: transparent;
    &:hover{
      opacity: .8;
    }
    img{
      width: .8125em;
      display: block;
    }
  }
}

.video{
  display: block;
  position: relative;
  &__img{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(29, 29, 27, 0.55) 0%, rgba(29, 29, 27, 0.55) 100%);
    }
    img{
      width: 100%;
      display: block;
    }
  }
  &__play{
    @include centerAbsolute;
    display: flex;
    align-items: center;
    z-index: 3;
    &:hover{
      img{
        opacity: 1;
      }
    }
    img{
      width: 5.1875em;
      opacity: .7;
      flex-shrink: 0;
      margin-right: 1.56em;
      transition: all .3s;
    }
    &-title{
      border-left: 1px solid rgba(#fff,.4);
      padding-left: 1.44em;
      min-height: 1.75em;
      display: flex;
      align-items: center;
    }
    .subtitle{
      color: #fff;
    }
  }
  &__content{
    position: absolute;
    bottom: 3.75em;
    left: 3.75em;
    width: 38.4em;
    max-width: 100%;
    z-index: 3;
    color: #FFF;
    @include grid100gap(1.2em);
    .subtitle{
      opacity: .7;
    }
  }
  &__title{
    font-size: 2.4375em;
    line-height: 133%;
  }
}

.white-bg{
  background-color: #fff;
}

._cover{
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(29, 29, 27, 0.50) 0%, rgba(29, 29, 27, 0.50) 100%);
    z-index: 2;
  }
}


@media screen and (max-width: 992px){
  .video{
    &__content{
      bottom: 0;
      left: 0;
      padding: 1em;
      gap: .5em;
      width: 100%;
    }
    &__title{
      font-size: 1.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .cart-alert{
    width: calc(100% - 2em);
    right: 1em;
    bottom: 1em;
    padding: 1em 1em 4.5em;
    &__img{
      width: 5em;
      height: 5.125em;
      margin-right: 1em;
    }
    &__nav{
      position: absolute;
      left: 0;
      bottom: 0;
      padding: 1em;
      width: 100%;
      .m-btn{
        width: 100%;
      }
    }
  }

  .video{
    &__play{
      img{
        width: 4em;
        margin: 0;
      }
      &-title{
        display: none;
      }
    }
    &__content{
      display: none;
    }
  }
}
