.text87{
  font-size: 5.4375em;
  line-height: 115%;
}

.text53{
  font-size: 3.3125em;
  line-height: 115%;
}

.text47{
  font-size: 2.9375em;
  line-height: 115%;
}

.text24{
  font-size: 1.5em;
  line-height: 146%;
}

.text21{
  font-size: 1.3125em;
  line-height: 143%;
}

.text18{
  font-size: 1.125em;
  line-height: 194%;
}

.text17{
  font-size: 1.0625em;
  line-height: 153%;
}

.text15{
  font-size: .9375em;
  line-height: 153%;
}

.subtitle{
  font-size: 1em;
  line-height: 125%;
  font-weight: 500;
  letter-spacing: 0.18em;
  text-transform: uppercase;
}

.thin-text{
  font-weight: 200;
}

.light-text{
  font-weight: 300;
}

.regular-text{
  font-weight: 400;
}

.medium-text{
  font-weight: 500;
}

.semi-bold-text{
  font-weight: 600;
}

.bold-text{
  font-weight: 700;
}

.extra-bold-text{
  font-weight: 800;
}

.gray-text{
  color: #A8A8A8;
}

.text{
  font-weight: 400;
  b{
    font-weight: 700;
  }
  p,li,ul{
    @include lastM(0 0 .25em);
  }
  p,li{
    font-size: 0.9375em;
    line-height: 193%;
  }
  ul{
    padding-left: .1875em;
    li{
      &:before{
        content: '•';
        margin-right: .1875em;
      }
    }
  }
}

.o-list{
  counter-reset: ol;
  padding-left: .3125em;
  font-weight: 300;
  @include grid100gap(.875em);
  li{
    counter-increment: ol;
    font-size: 1em;
    line-height: 181%;
    &:before {
      content: counter(ol) '. ';
      @include sameNumWidth;
      min-width: 1.125em;
    }
  }
}


@media screen and (max-width: 992px){
  .text87{
    font-size: 3em;
  }

  .text53,
  .text47{
    font-size: 2.25em;
  }

  .text18{
    font-size: 1em;
  }

  .text{
    p,li{
      font-size: .875em;
      line-height: 170%;
    }
  }
}


@media screen and (max-width: 576px){
  .text87{
    font-size: 2.5em;
  }

  .text53{
    font-size: 1.75em;
  }

  .text47{
    font-size: 1.5em;
  }

  .text24,
  .text21{
    font-size: 1.125em;
  }

  .text17{
    font-size: 1em;
  }

  .subtitle{
    font-size: .875em;
  }
}
