.about2{
  padding-bottom: 0;
  .about__nav{
    margin: 1.2em 0 0;
  }
}

.blog2{
  padding-bottom: 4em;
  &__grid{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 5.625em / 4));
    gap: 1.875em;
  }
}

.pagination{
  display: flex;
  justify-content: center;
  margin-top: 5em;
  &__item{
    height: 3.625em;
    padding: 0 1.56em;
    border: 2px solid #E9EAEE;
    @include center;
    margin: 0 .3125em;
    &:hover{
      border-color: $black2;
      color: $black2;
    }
    img{
      width: 1.1875em;
    }
    span{
      font-size: .9375em;
      line-height: 120%;
    }
    &._dots{
      pointer-events: none;
    }
  }
}

.guide-item{
  padding-bottom: 2.5em;
  &__wrap{
    margin-top: 3.5em;
    display: grid;
    grid-template-columns: calc(100% - 8.38em) 3.25em;
    justify-content: space-between;
  }
  &__media{
    margin-top: 4.87em;
    overflow: hidden;
  }
}

.article{
  &__img{
    display: block;
    width: 100%;
  }
}

.article-content{
  margin: 3.2em auto 0;
  width: 75.4375em;
  max-width: 100%;
  h3{
    font-size: 1.1875em;
    font-weight: 600;
    line-height: 116%;
    letter-spacing: 0.01188rem;
    @include lastM(0 0 .9em);
  }
  h4{
    font-size: 1em;
    line-height: 194%;
    font-weight: 700;
    @include lastM(0 0 1em);
  }
  p,li{
    font-size: 1.0625em;
  }
  ul,
  p{
    @include lastM(0 0 1.2em);
  }
  .video{
    margin: 2.5em 0 3.125em;
    &__img{
      &:before{
        background: linear-gradient(0deg,rgba(29,29,27,.72) 0,rgba(29,29,27,.72) 100%);
      }
    }
  }
  ul{
    padding-left: .5em;
    li{
      display: flex;
      &:before{
        content: '';
        flex-shrink: 0;
        width: .75em;
        height: .75em;
        background-image: url(../img/check.svg);
        @include bgImgSize(100%);
        transform: translateY(.5em);
        margin-right: .5em;
      }
    }
  }
}

.article-slider{
  margin-top: 0;
  &__img{
    display: block;
    @include imgCover(67.3%);
  }
}

.share{
  text-align: center;
  position: sticky;
  top: 1em;
  &__title{
    font-size: .6875em;
    line-height: 125%;
    color: #9B9B9B;
    text-align: center;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__link{
    margin-top: 1em;
    @include center;
    border: 1px solid #E6E6E6;
    border-radius: 50%;
    width: 3.25em;
    height: 3.25em;
    img{
      max-width: 1.25em;
      max-height: 1.375em;
    }
    &:hover{
      border-color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .blog2{
    padding-bottom: 2em;
    &__grid{
      margin-top: 2em;
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
  }

  .pagination{
    margin-top: 2.5em;
  }

  .guide-item{
    &__wrap{
      grid-template-columns: 100%;
      gap: 1.25em;
      margin-top: 1.5em;
    }
    &__col{
      &._order-1{
        order: -1;
      }
    }
    &__media{
      margin-top: 1em;
    }
  }

  .share{
    display: flex;
    align-items: center;
    &__link{
      margin: 0 0 0 .5em;
      width: 2.5em;
      height: 2.5em;
    }
  }

  .article-content{
    margin-top: 1em;
    p,li{
      font-size: .875em;
    }
    .video{
      margin: 1em 0;
    }
  }
}


@media screen and (max-width: 576px){
  .blog2{
    &__grid{
      grid-template-columns: 100%;
      gap: 2em;
    }
  }

  .pagination{
    &__item{
      height: 3em;
      padding: 0 1em;
      margin: 0 .25em;
      &._hide-mob{
        display: none;
      }
      span{
        font-size: .875em;
      }
      img{
        width: 1.0625em;
      }
    }
  }
}
