.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba(#1B1B1B, 0.60);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: #fff;
        width: 84em;
        max-width: calc(100% - 2.5em);
        position: relative;
        z-index: 3;
        overflow: hidden;
    }
    &__scroll{
        overflow-y: auto;
        overflow-x: hidden;
        max-height: calc(100 * var(--vh));
        background-image: url(../img/modal-bg.png);
        background-repeat: no-repeat;
        background-position: left bottom 2.56em;
        background-size: 42.125em auto;
    }
    &__top{
        padding: 2.5em;
    }
    &__close {
        position: absolute;
        border-top: 0;
        top: .94em;
        right: 1.5em;
        background-color: transparent;
        width: .75em;
        svg {
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                fill: $primary;
            }
        }
    }
    &__title{
        font-size: 1.6875em;
        line-height: 120%;
    }
    &__bottom{
        margin-top: .5em;
        padding: 3.125em;
        background: #F7F9FA;
        @include center;
        .m-btn{
            max-width: 100%;
            text-transform: uppercase;
        }
        .m-btn-black{
            width: 17.8125em;
        }
        .m-btn-black-outline,
        .m-btn-gray{
            padding: 0 1em;
            width: 13.5em;
            font-weight: 700;
            span{
                font-size: .9375em;
            }
        }
        .m-btn-gray:not(:hover){
            background: #ECECEC;
        }
    }
    &__btn{
        margin: 0 .6em;
        height: 4.0625em;
    }
}

.wallpaper{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: 44.0625em calc(100% - 44.0625em - 1.56em);
    gap: 1.56em;
    &__img{
        img{
            width: 100%;
            display: block;
        }
    }
}

.calc{
    padding-top: .56em;
    @include grid100gap(2.75em);
    &__title{
        margin-bottom: 1em;
        span{
            font-size: 1.1875em;
            line-height: 106%;
        }
    }
    &__size{
        display: grid;
        grid-template-columns: 10.8125em 10.8125em calc(100% - 25.375em);
        gap: 1.875em;
        align-items: center;
        &-hint{
            color: #3C3C3C;
        }
    }
    &__scale{
        display: flex;
        margin-top: 1.81em;
    }
    &__materials{
        margin-top: 1.69em;
        display: flex;
    }
}

.scale-field,
.material-field{
    position: relative;
    text-align: center;
    &__price-desc,
    &__title{
        font-size: .9375em;
        line-height: 133%;
    }
    .m-radio{
        label{
            z-index: 2;
            position: absolute;
            top: 0;
            left: 50%;
            padding-left: 1.5em;
            transform: translate(-50%,-50%);
            &:before{
                background-color: #fff;
            }
        }
    }
}

.scale-field{
    margin-right: 2.5em;
    &__rect{
        background: #F9F9F9;
        width: 4.4375em;
        height: 4.4375em;
        margin-bottom: .6875em;
        @include center;
        img{
            max-width: 100%;
            width: 2.5625em;
            display: block;
        }
    }
}

.material-field{
    width: 12.375em;
    margin-right: 1.625em;
    .m-radio{

        input:checked~.material-field__img{
            border-color: #000;
        }
    }
    &__img{
        border: 1px solid #D3D3D3;
        @include imgCover(67.7%);
    }
    &__info{
        margin-top: .69em;
        @include grid100gap(.37em);
    }
    &__price{
        @include center;
        &-title{
            font-size: 1.0625em;
            line-height: 118%;
        }
        &-desc{
            color: #8B9092;
            margin-left: .4em;
        }
    }
}

.result{
    padding: 2.94em 0 2.94em 2.94em;
    position: relative;
    &__img{
        img{
            display: block;
            max-width: 100%;
        }
    }
    &__item{
        position: absolute;
        top: 0;
        left: 0;
        padding-left: 2.94em;
        width: 100%;
        &-title{
            font-size: 0.75em;
            line-height: 133%;
            text-align: center;
            color: #646464;
        }
        &._bottom{
            top: unset;
            bottom: 0;
        }
        &._left{
            height: 100%;
            width: unset;
            padding: 2.94em 0;
            .result__item-inner{
                display: flex;
            }
            .result__item-title{
                writing-mode: tb-rl;
                transform: rotate(180deg);
            }
            .arrow{
                margin: 0 0 0 .47em;
                width: 1px;
                height: 100%;
                &:after{
                    top: unset;
                    bottom: 0;
                    left: 50%;
                    transform: translate(-4px, 0) rotate(-45deg);
                }
                &:before{
                    top: 0;
                    left: 50%;
                    transform: translate(-4px, 0) rotate(-45deg);
                }
            }
        }
    }
}

.arrow{
    background-color: #A8A8A8;
    height: 1px;
    width: 100%;
    margin-top: .47em;
    position: relative;
    &:after,
    &:before{
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        width: 8px;
        height: 8px;
        transform-origin: 50% 50%;
        transform: translate(0, -4px) rotate(45deg);
        border-radius: 0 0 0 0;
    }
    &:before{
        box-shadow: inset -1px 1px 0 0 #A8A8A8;
    }
    &:after{
        left: 0;
        right: unset;
        box-shadow: inset 1px -1px 0 0 #A8A8A8;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__scroll{
            overflow: unset;
            overflow-x: hidden;
        }
        &__top,
        &__bottom{
            padding: 1em;
        }
    }

    .wallpaper{
        grid-template-columns: 100%;
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__title{
            font-size: 1.25em;
        }
        &__bottom{
            @include grid100gap(.5em);
            .m-btn{
                width: 100%;
                padding: 0;
                margin: 0;
            }
        }
    }

    .calc{
        &__size{
            grid-template-columns: repeat(2,calc(50% - .5em));
            gap: 2em 1em;
            &-hint{
                grid-column: 1/-1;
                text-align: center;
            }
        }
        &__materials{
            display: grid;
            grid-template-columns: repeat(2,calc(50% - .25em));
            gap: .5em;
        }
        &__title{
            span{
                font-size: 1em;
            }
        }
    }

    .scale-field{
        margin-right: 1.25em;
    }

    .material-field{
        width: 100%;
        margin: 0;
    }

    .result{
        font-size: .75em;
        width: calc(100% + 1em);
        margin-left: -1em;
        &__item{
            &-title{
                font-size: .5em;
            }
        }
    }
}
