.inner-page{
  padding-bottom: 7.5em;
}

.pagination2{
  display: flex;
  justify-content: center;
  margin-top: 6.25em;
  &__more{
    width: 18.9375em;
    height: 4.0625em;
    font-weight: 500;
    span{
      font-size: 0.9375em;
      line-height: 120%;
      text-transform: uppercase;
      letter-spacing: 0.00938em;
    }
  }
}

.page-top{
  @include grid100gap(1.75em);
  max-width: 75em;
  .text15{
    line-height: 180%;
  }
}

.portfolio{
  &__grid{
    margin-top: 3.62em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .94em));
    gap: 1.88em;
  }
}


@media screen and (max-width: 992px){
  .inner-page{
    padding-bottom: 4em;
  }

  .pagination2{
    margin-top: 3em;
  }

  .page-top{
    gap: 1em;
  }

  .portfolio{
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .pagination2{
    &__more{
      width: 100%;
    }
  }
}
