.profile{
  &__inner{
    display: grid;
    grid-template-columns: 23.9375em calc(100% - 23.9375em - 1.875em);
    gap: 1.875em;
  }
  &__title{
    font-size: 1.625em;
    line-height: 120%;
  }
}

.personal-info{
  padding: 2.37em 3.13em 4.12em;
  background-color: #fff;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.6em));
    gap: 2em 3.2em;
    margin-top: 1.25em;
  }
  &__title{
    color: #616161;
    font-size: 1.125em;
    line-height: 111%;
  }
  &__fields{
    margin-top: 1.44em;
    @include grid100gap(1.5625em);
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 4.12em;
    .m-btn{
      height: 4.0625em;
      max-width: 100%;
      margin: 0 .6em;
    }
    .m-btn-black{
      width: 15.7em;
    }
    .m-btn-black-outline{
      width: 13.5em;
      span{
        font-size: .9375em;
      }
    }
  }
}

.favourite{
  .catalog__items{
    margin-top: 2em;
  }
}

.orders{
  background-color: #fff;
  padding: 2.37em 3.13em 3.12em;
  &__groups{
    margin-top: 1.5em;
    @include grid100gap(3.8em);
  }
}

.orders-group{
  &__top{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .94em 0;
    color: #616161;
    border-bottom: 1px solid #F0F0F0;
    .text18{
      line-height: 111%;
    }
  }
  &__nav{
    display: flex;
    justify-content: flex-end;
    margin-top: 2.81em;
    .m-btn{
      width: 15.6875em;
      max-width: 100%;
    }
  }
}

.order-card{
  padding: .94em 0;
  border-bottom: 1px solid #F0F0F0;
  display: grid;
  grid-template-columns: calc(100% - 25.6875em) 18em 5.6875em;
  gap: 1em;
  align-items: center;
  &__price{
    display: flex;
    align-items: center;
    &-title,
    &-desc{
      font-size: 1.0625em;
      line-height: 118%;
      white-space: nowrap;
    }
    &-title{
      color: #A3A3A3;
      margin-right: .5em;
    }
  }
  &__action{
    display: flex;
    align-items: center;
  }
  &__btn{
    width: 2.5em;
    height: 2.5em;
    border-radius: 50%;
    flex-shrink: 0;
    @include lastM(0 .625em 0 0);
    svg{
      width: .875em;
      height: auto;
    }
    &:hover{
      path{
        stroke: #fff;
      }
    }
  }
}

.order-info{
  display: flex;
  align-items: center;
  &__img{
    width: 8.75em;
    flex-shrink: 0;
    margin-right: 1.37em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: 1em;
    font-weight: 600;
    line-height: 187%;
    letter-spacing: 0.01em;
  }
  &__desc,
  &__size{
    font-size: 0.8125em;
    font-weight: 400;
    line-height: 180%;
    letter-spacing: 0.00813em;
    color: #575757;
  }
}

.orders-table{
  margin-top: 3.25em;
  table{
    width: 100%;
    border-spacing: 0;
  }
  th{
    height: 2.85em;
    padding: .5em 1.25em;
    border-bottom: 1px solid #EBEBEB;
    background: #FBFBFB;
    color: #333;
    text-align: left;
    span{
      font-size: .75em;
      line-height: 120%;
    }
  }
  td{
    padding: .95em 1.25em;
    border-bottom: 1px solid #F0F0F0;
  }
}

.order-row{
  td{
    &:nth-child(1){
      width: 6.4em;
    }
    &:nth-child(2){
      width: 31em;
    }
    &:nth-child(3){
      width: 20em;
    }
  }
  &__id{
    font-size: .875em;
    line-height: 142%;
    font-weight: 400;
  }
  &__status{
    display: flex;
    align-items: center;
    white-space: nowrap;
    i{
      width: 0.3125em;
      height: 0.3125em;
      flex-shrink: 0;
      border-radius: 50%;
      margin-right: .5em;
    }
    span{
      font-size: .875em;
      line-height: 143%;
    }
  }
  &__title{
    display: none;
  }
  .order-info{
    max-width: 31em;
  }
  &__btn{
    display: flex;
    align-items: center;
    white-space: nowrap;
    &-title{
      color: #5F5F5F;
      font-size: 0.875em;
      line-height: 143%;
    }
  }
}

.checkout{
  margin-top: 1.5em;
  display: grid;
  grid-template-columns: calc(100% - 20.75em - 1.94em) 20.75em;
  gap: 1.94em;
  .order-card{
    @media screen and (min-width: 577px){
      grid-template-columns: calc(100% - 15.6875em) 8em 5.6875em;
    }
  }
}

.checkout-form{
  margin-top: 3.25em;
  max-width: 33em;
  @include grid100gap(3.25em);
}

.checkout-group{
  .personal-info__fields{
    margin-top: 1.81em;
  }
  &__radios{
    margin-top: 2em;
    @include grid100gap(1.06em);
  }
}

.delivery-item{
  display: flex;
  align-items: center;
  .m-radio{
    margin-right: .5em;
    flex-shrink: 0;
  }
  &__img{
    width: 4.25em;
    margin-right: .75em;
    flex-shrink: 0;
    img{
      max-width: 100%;
      display: block;
    }
  }
  &__price{
    margin-left: auto;
    padding-left: 1em;
  }
}

.checkout-sum{
  padding: 1.875em;
  background: #FAFAFA;
  position: sticky;
  top: 1em;
  &__field{
    position: relative;
    input{
      width: 100%;
      height: 4.08em;
      font-size: .8125em;
      border: 1px solid #E8E8E8;
      background: #FFF;
      padding-left: 1.38em;
      padding-right: 7.7em;
      &::placeholder{
        color: #989898;
      }
    }
    &-btn{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: .375em;
      width: 5.5625em;
      height: 2.5625em;
      font-weight: 700;
      span{
        font-size: .875em;
      }
    }
  }
  &__items{
    margin-top: 1.44em;
    margin-bottom: 1.12em;
    padding-bottom: 1.25em;
    border-bottom: 1px solid #E2E2E2;
    @include grid100gap(.69em);
  }
  &__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-size: .875em;
    line-height: 143%;
  }
  &__desc{
    font-weight: 600;
    font-size: .9375em;
    line-height: 133%;
    &._big{
      font-size: 1.1875em;
      line-height: 105%;
    }
    &._red{
      color: #BD2E2E;
    }
  }
  &__btn{
    width: 100%;
    margin-top: 1.8em;
  }
}

.profile-sidebar{
  background-color: #fff;
  &__close{
    display: none;
  }
}

.user{
  padding: 1.875em 2.5em;
  display: flex;
  align-items: center;
  &__img{
    margin-right: 1.2em;
    width: 3.625em;
    height: 3.625em;
    flex-shrink: 0;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }
  &__logout{
    display: flex;
    align-items: center;
    margin-top: .44em;
    color: #A5A5A5;
    font-weight: 600;
    &:hover{
      color: $black2;
    }
    img{
      margin-right: .44em;
      flex-shrink: 0;
      width: .9375em;
    }
    span{
      font-size: .875em;
      line-height: 143%;
    }
  }
}

.contacts{
  @include grid100gap(1.75em);
  padding: 1.625em 1.56em;
  border-top: 1px solid #F2F2F2;
  font-weight: 500;
  &__item{
    display: flex;
  }
  &__icon{
    width: 1.125em;
    flex-shrink: 0;
    margin-right: .69em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.3125em);
  }
  &__title{
    font-size: 0.875em;
    line-height: 143%;
    color: #6F6F6F;
  }
  &__phone{
    font-size: 1.1875em;
    line-height: 106%;
  }
  &__mail{
    text-decoration: underline;
  }
  &__address{
    font-size: 1em;
    line-height: 169%;
  }
}

.profile-link{
  border-top: 1px solid #F2F2F2;
  display: flex;
  align-items: center;
  padding: 0 2.06em 0 1.56em;
  height: 4.625em;
  &:hover,
  &._active{
    background-color: $black2;
    border-color: $black2;
    color: #FFF;
    path{
      stroke: #fff;
    }
  }
  &__icon{
    width: 1.25em;
    height: 1.25em;
    margin-right: .625em;
    flex-shrink: 0;
    @include center;
    svg{
      max-height: 100%;
      max-width: 100%;
      width: auto;
      height: auto;
    }
  }
  &__num{
    margin-left: auto;
    font-size: .75em;
    min-width: 2em;
    height: 2em;
    padding: 0 .125em;
    @include center;
    background-color: #FFE498;
    border-radius: 50%;
    font-weight: 600;
    color: $black2;
  }
}

.mob-user{
  display: none;
}


@media screen and (max-width: 992px){
  .profile{
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .personal-info{
    padding: 1em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 2em;
    }
    &__fields{
      gap: 1em;
      margin-top: 1em;
    }
    &__nav{
      margin-top: 2em;
    }
  }

  .orders{
    padding: 1em;
  }

  .order-card{
    grid-template-columns: calc(100% - 16.6875em) 9em 5.6875em;
  }

  .orders-table{
    th,
    td{
      padding-left: .75em;
      padding-right: .75em;
    }
  }

  .order-row{
    td{
      &:nth-child(2){
        width: 22em;
      }
      &:nth-child(3){
        width: 8em;
      }
    }
  }

  .checkout{
    grid-template-columns: 100%;
    gap: 1.5em;
  }

  .profile-sidebar{
    z-index: 30;
    transform: translateX(-150%);
    transition: all .4s;
    overflow-y: auto;
    top: 0;
    left: 0;
    width: 100%;
    height: $fullHeight;
    position: fixed;
    &._active{
      transform: translateX(0%);
    }
    &__close{
      position: fixed;
      top: 1.125em;
      right: 1.125em;
      width: .75em;
      display: flex;
      img{
        width: 100%;
        display: block;
      }
    }
  }

  .mob-user{
    display: block;
    background-color: #fff;
    .user{
      padding-top: .94em;
      padding-bottom: .94em;
    }
  }

  .user{
    padding-left: 1.56em;
    padding-right: 1.56em;
    &__content{
      margin-right: .5em;
    }
    &__btn{
      margin-left: auto;
      width: 1.75em;
      height: 1.75em;
      flex-shrink: 0;
      border-radius: 50%;
      @include center;
      background-color: #F7F9FA;
      img{
        width: .5em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .personal-info {
    &__grid {
      margin-top: 2em;
      grid-template-columns: 100%;
    }
    &__nav{
      display: grid;
      grid-template-columns: 100%;
      gap: .5em;
      .m-btn{
        width: 100%;
        margin: 0;
      }
    }
  }

  .orders-group{
    &__nav{
      margin-top: 1.5em;
      .m-btn{
        width: 100%;
      }
    }
  }

  .order-card{
    grid-template-columns: 100%;
    &__action{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
      width: 100%;
    }
    &__btn{
      width: 100%;
      border-radius: 1em;
    }
  }

  .order-info{
    &__img{
      width: 6em;
      margin-right: 1em;
    }
    &__content{
      @include grid100gap(.25em)
    }
    &__title,
    &__desc,
    &__size{
      line-height: 120%;
    }
  }

  .orders-table{
    margin-top: 1.5em;
    tbody,
    table{
      display: block;
    }
    thead{
      display: none;
    }
  }

  .order-row{
    @include grid100gap(1em);
    padding: .94em 0;
    border-bottom: 1px solid #f0f0f0;
    td{
      border: none;
      padding: 0;
      width: 100% !important;
    }
    td{
      display: flex;
      align-items: center;
    }
    &__title{
      display: block;
      color: #A3A3A3;
      font-size: 1.0625em;
      line-height: 118%;
      margin-right: .5em;
    }
    .order-card__btn{
      width: 2.5em;
    }
  }

  .checkout-sum{
    padding: 1em;
  }
}
