.fg {
  position: relative;
  margin: 0;
  select,
  input {
    border: none;
    border-bottom: 1px solid #959595;
    padding: 1.25em .625em 0;
    height: 3.75em;
    width: 100%;
    font-size: 1em;
    background-color: transparent;
    color: #000;
    &::placeholder {
      color: #000;
    }
    &.input-active,
    &:focus {
      &~label{
        top: .3em;
      }
    }
  }
  select{
    background-image: url(../img/arrow6.svg);
    background-repeat: no-repeat;
    background-size: .75em auto;
    background-position: right center;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    &::-ms-expand {
      display: none; // IE
    }
  }
  label{
    position: absolute;
    top: 1.25em;
    left: 0;
    padding: 0 .625em;
    width: 100%;
    white-space: nowrap;
    color: #C0C0C0;
    pointer-events: none;
    transition: all .2s;
    span{
      font-size: .9375em;
      line-height: 133%;
    }
  }
  &._required{
    label:after{
      content: ' *';
      color: #EC1D1D;
    }
  }
  .pass-toggle{
    position: absolute;
    width: 1.1875em;
    height: 0.8125em;
    right: 0;
    top: 1.44em;
    svg{
      @include centerAbsolute;
      width: 100%;
      height: auto;
    }
    ._view1{
      opacity: 0;
    }
    &._active{
      ._view2{
        opacity: 0;
      }
      ._view1{
        opacity: 1;
      }
    }
  }
}

.m-radio,
.filter-color,
.m-check,
.sort-filter__item{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.filter-color,
.m-check{
  input{
    &:checked+label {
      &::before {
        background-image: url(../img/check2.svg);
      }
    }
  }
  label{
    &:before{
      @include bgImgSize(.5625em);
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: #232323;
        border-color: #232323;
      }
    }
  }
  label {
    padding-left: 2em;
    position: relative;
    min-height: 1.875em;
    cursor: pointer;
    display: block;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border: 1px solid #DFDFDF;
      width: 1.1875em;
      height: 1.1875em;
      left: 0;
      top: .34em;
      transition: all .100s;
    }
    span{
      font-size: .9375em;
      line-height: 200%;
    }
  }
}

.m-radio{
  input{
    &:checked+label{
      &:before{
        border-color: #000;
      }
      &:after{
        opacity: 1;
      }
    }
  }
  label{
    position: relative;
    padding-left: 2.25em;
    min-height: 1.5em;
    padding-top: .28em;
    display: block;
    &:before,
    &:after{
      content: '';
      position: absolute;
      transition: all .1s;
      border-radius: 50%;
      width: 1.5em;
      height: 1.5em;
      top: 0;
      left: 0;
    }
    &:before{
      border: 1px solid #DEDEDE;
    }
    &:after{
      background-image: url(../img/radio.svg);
      @include bgImgSize(.625em);
      opacity: 0;
    }
  }
  span{
    font-size: .9375em;
    line-height: 100%;
  }
}
