@import "../vars.scss";
.breadcrumbs {
    padding: 1.625em 0;
    &__item {
        font-weight: 400;
        font-size: .875em;
        line-height: 135%;
        position: relative;
        display: inline;
        color: #636363;
        &:last-child {
            color: #2B2B2B;
        }
        &:not(:first-child) {
            margin-left: 2.9em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: .78em;
                height: 1.357em;
                background-image: url(../img/bread-line.svg);
                @include bgImgSize(100%);
                top: 0;
                right: 100%;
                margin-right: 1em;
            }
        }
    }
}
