.custom{
  padding-bottom: 9.94em;
}

.custom-info{
  margin-top: 2.4em;
  display: grid;
  grid-template-columns: calc(50% - 3.9em) 7.8em calc(50% - 3.9em);
  align-items: flex-end;
  &__left{
    padding-left: 9em;
    padding-right: 4em;
  }
  .text47{
    line-height: 138%;
  }
  .text15{
    line-height: 180%;
    color: $black2;
  }
  &__right{
    padding-right: 4.3em;
    padding-left: 8.9em;
  }
}

.wheel{
  position: relative;
  &__arrow{
    @include centerAbsolute;
    @include center;
    width: 4.0625em;
    height: 4.0625em;
    background: #F5F5F5;
    border-radius: 50%;
    img{
      width: .6875em;
    }
  }
  &__words{
    img{
      max-width: 100%;
      width: 100%;
      display: block;
      animation: 8s linear 0s normal none infinite running rotate;
    }
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.custom-detail{
  margin-top: 3.25em;
  display: grid;
  grid-template-columns: 75.4375em calc(100% - 75.4375em);
  align-items: center;
  .about3{
    padding: 6.31em 9em 4.31em;
    .text53{
      font-size: 2.4375em;
      line-height: 133%;
    }
    &__subtitle{
      margin-bottom: 1.2em;
    }
    &__nav{
      margin-top: 6em;
    }
  }
}

.examples-info,
.custom-hint{
  &__title{
    font-size: 1.5625em;
    line-height: 136%;
  }
}

.custom-hint{
  padding: 2.19em 3.13em 3.25em;
  background-color: #fff;
  width: calc(100% + 6.38em);
  margin-left: -6.38em;
  position: relative;
  z-index: 2;
  &__items{
    margin-top: .88em;
    color: #000;
  }
  &__btn{
    margin-top: .88em;
    width: 100%;
  }
}

.examples{
  display: grid;
  grid-template-columns: 34.37em calc(100% - 34.37em);
  align-items: center;
  margin-top: 9.94em;
  .works-slider{
    width: 100%;
    margin: 0;
  }
  &__nav{
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    border-left: 1px solid #F5F5F5;
  }
}

.examples-info{
  position: relative;
  z-index: 2;
  width: calc(100% + 2.685em);
  background-color: #fff;
  padding: 2.5em 5.5em 2.5em 3.13em;
  .text17{
    margin-top: 1.25em;
    color: #333;
    padding-right: 1em;
  }
}

.custom-upload{
  padding: 5em 0;
  .text47{
    text-align: center;
  }
}

.custom-form{
  width: 76.25em;
  max-width: 100%;
  margin: 3em auto 0;
  display: grid;
  grid-template-columns: 33em calc(100% - 35.5em);
  gap: 2.5em;
  &__nav,
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .8125em));
    gap: 1.625em;
    ._full{
      grid-column: 1/-1;
    }
  }
  &__nav{
    margin-top: 1.625em;
  }
}

.capcha{
  img{
    width: 100%;
    display: block;
  }
}

.img-upload{
  padding: 2.69em 1.44em 1.44em;
  border: 1px dashed #CECECE;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  input{
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
    opacity: 0;
  }
  &__drop{
    @include grid100gap(1.06em);
    text-align: center;
    img{
      width: 2.8125em;
      display: block;
      margin: 0 auto;
    }
  }
  &__or{
    color: #767676
  }
  .text15{
    line-height: 133%;
  }
  &__btn{
    margin: 1.06em auto 0;
    width: 10.5em;
    max-width: 100%;
    height: 2.92em;
    padding: 0 1.44em;
    svg{
      width: 1.1875em;
      height: auto;
      margin-right: .625em;
    }
    span{
      font-size: .9375em;
    }
  }
  &__items{
    display: flex;
    justify-content: space-between;
    margin-top: auto;
    padding-top: 2em;
  }
  &__item{
    font-weight: 400;
    text-transform: uppercase;
    color: #726F6F;
    span{
      font-size: .75em;
      line-height: 167%;
    }
  }
}


@media screen and (max-width: 992px){
  .custom{
    overflow: hidden;
    padding-bottom: 2em;
  }

  .custom-info{
    grid-template-columns: 100%;
    gap: 1em;
    &__center{
      order: 2;
      margin: 0 auto;
      width: 7em;
    }
    &__left,
    &__right{
      padding: 0;
    }
  }

  .custom-detail{
    margin-top: 2em;
    grid-template-columns: 100%;
    gap: 1em;
    .about3{
      padding: 3em 1em;
      .text53{
        font-size: 2.25em;
      }
    }
  }

  .custom-hint{
    width: 30em;
    max-width: 100%;
    margin: 0 auto;
  }

  .examples{
    grid-template-columns: 100%;
    gap: 1em;
    margin-top: 3em;
    .swiper-container{
      overflow: unset;
    }
  }

  .examples-info{
    width: 100%;
    padding: 1em;
  }

  .custom-upload{
    overflow: hidden;
    padding: 2em 0;
  }

  .custom-form{
    grid-template-columns: 100%;
    margin-top: 1.5em;
    &__col{
      &:nth-child(2){
        order: -1;
      }
    }
    &__nav{
      .m-btn{
        min-height: 100%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .custom-detail{
    .about3{
      .text53{
        font-size: 1.75em;
      }
      &__nav{
        margin-top: 2em;
      }
    }
  }

  .custom-hint{
    padding: 1em;
  }

  .custom-form{
    &__nav,
    &__fields{
      grid-template-columns: 100%;
      gap: 1em;
    }
  }

  .img-upload{
    &__items{
      flex-direction: column;
      align-items: center;
    }
    &__item{
      @include lastM(0 0 1em);
    }
  }
}
